.aos-init[data-aos][data-aos].aos-animate {
    transform: unset;
}

.Home {
    background-color: rgb(0, 0, 30);
    width: 100%;
    padding-left: 10%;
    padding-right: 10%;
}

.Home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5em;
    margin-bottom: 5em;
}

.Home-container .Home-section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 5em;
    grid-auto-rows: minmax(100px, auto);
    background-color: rgb(0, 0, 30);;
    border-radius: 10px;
    margin: 3em;
    padding: 10px;
}

.Home-container .Home-sections .Home-hr {
    border: 1px solid rgba(175, 175, 175, 0.192);
    width: 75%;
    margin: auto;
    z-index: -99;
}

.Home-title h1 {
    font-size: 2.3em;
    font-weight: bold;
    color: white;
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    letter-spacing: 1px;
}

.Home-description p {
    margin-top: 0.5em;
    font-size: 1.2em;
    font-weight: bold;
    color: white;
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    letter-spacing: 1px;
}

.Home-sections {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 5em;
    padding-right: 5em;
}

.Home-container .Home-section img {
    width: 30em;
    height: auto;
    padding: 10px;
}

.Home-container .Home-section .Home-section-left, .Home-section .Home-section-right {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 10px;
    width: 30em;
}

.Home-container .Home-section .Home-section-right p, .Home-section-right h4 {
    text-align: end;
    width: 100%;
}

.Home-container .Home-section p {
    font-size: 1em;
    text-align: justify;
    margin: 0;
    margin-right: 2em;
    margin-top: 1em;
}

.Home-container .Home-download-title {
    font-size: 1.5em;
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    letter-spacing: 3px;
    margin-top: 2em;
}

/* CSS for scroll bar */

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background-color: black;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background-color: white;
    border-radius: 3px;
}


@media (max-width: 1325px) {
    .Home-container .Home-section {
        margin-top: 3em;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .Home-container .Home-section .Home-section-left, .Home-section .Home-section-right {
        width: 50%;
    }

    .Home-container .Home-section .Home-section-left img, .Home-section .Home-section-right img {
        width: 100%;
    }

    .Home-container .Home-section .Home-section-left p, .Home-section .Home-section-right p {
        margin: 0;
        margin-right: 0;
        margin-top: 1em;
    }
}

@media (max-width: 768px) {
    .Home-container .Home-section {
        grid-template-columns: 1fr;
        margin: 1em;
    }

    .Home-container .Home-section .Home-section-left, .Home-section .Home-section-right {
        width: 100%;
    }

    .Home-container .Home-section .Home-section-left img, .Home-section .Home-section-right img {
        width: 100%;
    }

    .Home-container .Home-section .Home-section-left p, .Home-section .Home-section-right p {
        margin: 0;
        margin-right: 0;
        margin-top: 1em;
    }

    .Home-container .Home-download-title {
        font-size: 1.2em;
    }
}

.carousel-item {
    height: 300px;
}

.Formation {
    background-color: lightgray;
    padding-top: 30px;
    padding-bottom: 30px;
}

.Formation-h1 {
    color: red;
    text-align: center;
    padding-top: 30px;
}

.Formation-h5 {
    color: black;
    text-align: center;
    padding-bottom: 30px;
}

.Formation-Button {
    background-color: white;
    border-width: 3px;
    border-radius: 0;
    padding: 10px, 20px, 10px, 20px;
    z-index: 1;
}

.Formation-Button.Website-Epitech {
    color: red;
    border-color: red;
}

/* ============================================= */

.blob-btn.blob-btn-Keimyung:hover {
    color: #FFFFFF;
}

.blob-btn.blob-btn-Keimyung {
    color: #0073FF;
}

.blob-btn.blob-btn-Keimyung::before {
    border: 3px solid #0073FF;
}

.blob-btn__blob-Keimyung {
    background-color: #0073FF;
}

.blob-btn.blob-btn-Epitech:hover {
    color: #FFFFFF;
}

.blob-btn.blob-btn-Epitech {
    color: red;
}

.blob-btn.blob-btn-Epitech::before {
    border: 3px solid red;
}

.blob-btn__blob-Epitech {
    background-color: red;
}

.Formation-Information-btn {
    padding-bottom: 15px;
}

.Formation-Information-btn button {
    width: auto;
    color: black;
    border: none;
    background-color: rgba(99, 99, 99, 0.192);
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 10px;
    margin-right: 10px;
    border-radius: 15px;
    cursor: default;
}